@mixin mQ($width) {
  @media (min-width: $width){
    @content
  }
}
@mixin mQLandscape {
  @media (min-width: 1024px) and (orientation: landscape),(min-width: 1200px){
    @content
  }
}

@font-face {
  font-family: 'Untitled Sans Test';
  src: url('../../fonts/UntitledSansTest-Regular.eot');
  src: url('../../fonts/UntitledSansTest-Regular.eot?#iefix') format('embedded-opentype'),
  url('../../fonts/UntitledSansTest-Regular.woff2') format('woff2'),
  url('../../fonts/UntitledSansTest-Regular.woff') format('woff'),
  url('../../fonts/UntitledSansTest-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

